body {
  text-align: center;
}
.trombone {
  margin: 0 0 2em 0;
  list-style-type: none;
  padding: 0;
  width: 15em;
}
.trombone {
  cursor: pointer;
  position: relative;
  left: 20px;
  background-color: #EEE;
  margin: .5em;
  padding: .3em 0;
  height: 1.6em;
  border-radius: 4px;
}
.trombone .text {
  position: relative;
}



